/* ===================================
========= register page  */
@import url('https://fonts.googleapis.com/css2?family=Kanit&family=Pacifico&family=Playfair+Display+SC&family=Poppins:wght@300&family=Prompt:wght@200&family=Roboto&family=Schibsted+Grotesk:wght@600&display=swap');

.form-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 83vh;
  flex-direction: column;
  background-color: #ffdee9;
  background-image: linear-gradient(0deg, #faf6de 0%, #faf6de 100%);
 background-size: cover;
}
.form-containera {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 83vh;
  flex-direction: column;
  background-color: #ffdee9;
  background-image: linear-gradient(0deg, #faf6de 0%, #faf6de 100%);
 background-size: cover;
}

.form-container form {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 45px;
  padding: 28px;
  background-color: #f8f8ff;
  min-height: fit-content;
  min-width: 36vh;
  border-radius: 10px;
  border: solid 1px #444040;
}


.form-containera form {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 45px;
  padding: 28px;
  background-color: #f8f8ff;
  min-height: fit-content;
  min-width: 40vh;
  border-radius: 10px;
  border: solid 1px #444040;
}


.form-container form .title {
  font-size: 26px;
  font-weight: 600;
  text-align: center;
  padding-bottom: 6px;
  color: black;
  text-shadow: 1.5px 1.5px 1.5px black;
  border-bottom: solid 2px black;
  font-family: 'Playfair Display SC', serif;
}

.form-containera form .title {
  font-size: 26px;
  font-weight: 600;
  text-align: center;
  padding-bottom: 6px;
  color: black;
  text-shadow: 1.5px 1.5px 1.5px black;
  border-bottom: solid 2px black;
  font-family: 'Playfair Display SC', serif;
}
.form-container form .mb-3 input {
  border: none;
  background-color: transparent;
  margin-top: 30px;
  padding: 14px 14px 14px 14px;
  width: 100%;
  border-bottom:solid 2px #444040;
}
.form-containera form .mb-3 input {
  border: none;
  background-color: transparent;
  margin-top: 30px;
  padding: 14px 14px 14px 14px;
  width: 100%;
  border-bottom:solid 2px #444040;
}
::placeholder{
  color:black;
  font-size: 16px;
 font-family: Arial, Helvetica, sans-serif;
 margin-bottom: 1px;
 font-weight: 500;

}

.form-container form .btn {
  margin-top: 16px;
  border: 2px solid #444040;
  border-radius: 0;
  color: white;
  width: 250px;
  text-align: center;
  padding: 15px 5px 15px 5px;
  width: 100%;
  background-color:#444040 ;
  font-family: Arial, Helvetica, sans-serif;
}
.form-containera form .btn {
  margin-top: 16px;
  border: 2px solid #444040;
  border-radius: 0;
  color: white;
  width: 250px;
  text-align: center;
  padding: 15px 5px 15px 5px;
  width: 100%;
  background-color:#444040 ;
  font-family: Arial, Helvetica, sans-serif;
}

.reg{
  font-size: 20px;
  font-weight: bolder;
  font-family: 'Kanit', sans-serif;
  letter-spacing: 4px;
}

.form-container form .btn:hover {
  cursor: pointer;
  border: 1px solid #444040;
  background: #000000; /* fallback for old browsers */
 transform: scale(1.05);
}
.form-containera form .btn:hover {
  cursor: pointer;
  border: 1px solid #444040;
  background: #000000; /* fallback for old browsers */
 transform: scale(1.05);
}

.batna{
  background: none;
  border: none;
  color:black;
  padding-top: 7px;
  font-size: 19px;
  text-decoration: none;
}

.batna:hover{
  cursor: pointer;
  font-size: 17px;
  transition: 1s;
}

 .mb {
  border:none;
 margin-top: 10px;
  padding: 14px 20px 14px 14px;
  width: 100%;
  margin-left: 0px;

  text-align: center;
}

.form-container form .btna {
  margin-top: 22px;
  border: 2px solid #444040;
  border-radius: 0;
  color: white;
  width: 250px;
  text-align: center;
  padding: 18px 5px 1px 5px;
  width: 100%;
  background-color:#444040 ;
  font-family: Arial, Helvetica, sans-serif;
}

.form-container form .btna:hover {
  cursor: pointer;
  border: 1px solid #444040;
  background: #000000; /* fallback for old browsers */
 transform: scale(1.05);
}

.forget{
  margin-top: 20px;
  color: red;
}