  width: 100%;
  height: 100vh;
  top:80px;
  left:-100%;
  transition: all .5s;
  
  text-align: center;
}

.menuu ul li{
  display: block;

}

.menuu checked ~ ul{
left:0;
}

.productdetail{
  height: 600px;
  display:flex;
  flex-direction: row;
  margin-top: 100px;
  margin-left: 100px;
  margin-right: 200px;
  gap:100px;
  border-bottom: 3px solid gray;
  overflow-x: hidden;
}
.a-flex{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap:170px;
  margin-top: 40px;
  margin-left: 200px;
}
.proimg{
  padding-top: 50px;
  padding-left: 40px;
  padding-bottom: 30px;
}

.proimg img{
  border: solid #c8c8cc 5px;
  border-radius: 9px;
  width: 350px;
}

.pdel{
  margin-left: 5px;
  width: 470px;
  display: flex;
  flex-direction: column;
  gap:30px;
  margin-top: 20px;
 
}

.pdel h1{
  text-align: center;
  color: gray;
  font-weight: 900;
  font-family: 'Headland One', serif;
  font-size: 2rem;
  border-bottom: #b6adad solid 3px;
  border-spacing: 3px;
}

.btndangerms-10{
  color:whitesmoke;
  background-color: #444040;
  padding: 10px 15px 10px 15px;
  border-radius: 5px;
  margin-top: 14px;
  cursor: pointer;
}

.btndangerms-10:hover{
  transform: scale(1.05);
}

.c-flex{
    display: flex;
    flex-wrap: wrap;
    gap:90px;
    margin-top: 40px;
  margin-left: 220px;
}

.allp h1{
  margin-left: 200px;
  color: gray;
}

.ew{
  display:none;
}

.searchbox{
  margin-top: 11px;
  padding: 5px 5px 5px 5px;
  height: 50px;
  width: 350px;
  margin-left: 30px;
  border-radius: 6px;
  border-right: none;
}

.mainnav{
  height:95px;
  padding: 8px 30px 24px 8px;
  font-size: 17px;
  overflow-x: hidden;
}
.btnbtn-warning{
  margin-top: 130px;
  color: black;
  font-weight: bold;
  font-size: 20px;
  border: none;
  background-color: transparent;
  cursor: pointer;
    }
    .m-2p-3{
      display: flex;
      margin-left: 370px;
    }
}

